import React, {
  ReactElement,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { Picker } from 'antd-mobile';
import { BottomBlank } from '@/components/TabBar/styles';
import { useLazyQuery } from '@apollo/client';
import { FIND_RANK } from '@/helpers/APIHelper';
import { PickValueArr } from '@/pages/Library/utils/stringArrToPickArr';
import dw from '@/pages/UserCenter/images/avatar-default.jpg';
import {
  IconArrow,
  ItemFirst,
  ItemSecond,
  ItemThird,
  LadderBox,
  LadderContent,
  RankAvatar,
  RankHeader,
  RankIcon,
  RankItem,
  RankItemCenter,
  RankItemEnd,
  RankItemStart,
  RankList,
  RankName,
  SelectItem,
} from '../styles';
import { LadderProps, RankItemTypes } from '../types';

const Ladder: React.FC<LadderProps> = (props) => {
  const { subjectArr, rankNum, subjectPointsArr } = props;

  // 
  const [getRank, { data: findRankData }] = useLazyQuery(FIND_RANK, {
    fetchPolicy: 'cache-and-network',
  });
  // 
  const rankArr = useMemo(() => {
    return findRankData?.findRank || [];
  }, [findRankData?.findRank]);
  console.log('findRankData++++', rankArr);
  // 
  const [rankValue, setRankValue] = useState<string[]>(['']);
  // 
  const [rankData, setRankData] = useState<PickValueArr[]>([]);
  const [showMy, setShowmy] = useState(0);

  // 
  const getPointsLabel = (): string => {
    let pointsLabel = '';
    rankData.forEach((item) => {
      if (item.value === rankValue[0]) {
        pointsLabel = item.label;
      }
    });
    return pointsLabel;
  };
  // 
  const getRankItem = (): ReactElement[] => {
    return rankArr?.rankSet?.map((item: RankItemTypes, index: number) => {
      const { avatar, rank, userName, point, isHigher } = item;
      return (
        <RankItem className={index + 1 === showMy ? 'my' : ''} key={rank}>
          <ItemFirst>
            <RankItemStart>{rank}</RankItemStart>
          </ItemFirst>
          <ItemSecond pl="12px">
            <RankItemCenter>
              <RankAvatar img={avatar || dw} />
              <RankName>{userName}</RankName>
            </RankItemCenter>
          </ItemSecond>
          <ItemThird>
            <RankItemEnd>
              {point}
              {isHigher > 0 && <RankIcon className="iconfont icon-up" />}
              {isHigher === 0 && (
                <RankIcon className="iconfont icon-no-change" />
              )}
              {isHigher < 0 && <RankIcon className="iconfont icon-down" />}
            </RankItemEnd>
          </ItemThird>
        </RankItem>
      );
    });
  };

  // 
  const getSubjectRank = useCallback(
    (subject: string) => {
      console.log(subjectPointsArr);
      if (subject) {
        subjectPointsArr.forEach((item) => {
          if (item.subject === subject) {
            setShowmy(item.subjectRankThisWeek);
          }
        });
      } else {
        setShowmy(rankNum);
      }
    },
    [rankNum, subjectPointsArr]
  );
  // 
  useEffect(() => {
    console.log('subjectArr+++++++', subjectArr);
    if (subjectArr?.length > 0) {
      const initData = [{ value: '', label: '' }, ...subjectArr];
      setRankData(initData);
    }
  }, [subjectArr]);

  // 
  useEffect(() => {
    let param = {};
    if (rankValue[0]) {
      param = {
        subject: rankValue[0],
      };
    }
    getSubjectRank(rankValue[0]);
    getRank({
      variables: param,
    });
  }, [getRank, getSubjectRank, rankValue]);

  return (
    <LadderBox>
      <LadderContent>
        <RankHeader>
          <ItemFirst></ItemFirst>
          <ItemSecond pl="100px"></ItemSecond>
          <ItemThird>
            <Picker
              data={rankData}
              cols={1}
              value={rankValue}
              onOk={(value: string[]): void => setRankValue(value)}
            >
              <SelectItem>
                <span>{getPointsLabel()}</span>
                <IconArrow className="iconfont icon-down-arrow" />
              </SelectItem>
            </Picker>
          </ItemThird>
        </RankHeader>
        <RankList>
          {getRankItem()}
          <BottomBlank />
        </RankList>
      </LadderContent>
    </LadderBox>
  );
};

export default Ladder;
