export interface FilterSourceTypeProps {
  date: string;
  sourceType: string;
  subject: string;
  _id: string;
  fullPoints: number;
  timeLimit: number;
}
interface FilterSourceTypeReturn {
  sourceType: string;
  children: FilterSourceTypeProps[];
}
export function filterSourceType(
  arr: FilterSourceTypeProps[]
): FilterSourceTypeReturn[] {
  const comMap: { [key: string]: FilterSourceTypeProps[] } = {}; // 
  const resultArr: FilterSourceTypeReturn[] = []; // 
  arr?.forEach((item) => {
    const { sourceType } = item;
    if (comMap[sourceType as keyof typeof comMap]) {
      comMap[sourceType as keyof typeof comMap].push(item);
    } else {
      comMap[sourceType as keyof typeof comMap] = [{ ...item }];
    }
  });
  Object.keys(comMap).forEach((key) => {
    resultArr.push({
      sourceType: key,
      children: comMap[key],
    });
  });
  return resultArr;
}

export interface FilterExamIdProps {
  bestPoints: number;
  examId: string;
  frequency: number;
  previousPoints: number;
  subject: string;
  timeCounter: number;
  userId: string;
}
interface FilterExamIdReturn {
  [key: string]: FilterExamIdProps;
}
export function filterExamId(arr: FilterExamIdProps[]): FilterExamIdReturn {
  const resultMap: FilterExamIdReturn = {};
  arr?.forEach((item) => {
    const { examId } = item || {};
    resultMap[examId as keyof typeof resultMap] = item;
  });
  return resultMap;
}
