import EXIF from './exif';
// 
const checkImg = (file: File): boolean => {
  return /\.(png|jpg|jpeg|PNG|JPG|JPEG)$/.test(file.name);
};

// 
function getPhotoOrientation(img: HTMLImageElement): number {
  let orient = 0;
  try {
    EXIF.getData(img, (): void => {
      orient = EXIF.getTag(img, 'Orientation');
    });
  } catch (e) {
    console.log(e);
  }
  return orient;
}
// 
function compressImg(imgBase64: string, sizeNum: number): Promise<string> {
  return new Promise((resolve, reject) => {
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const img = new Image();
    img.src = imgBase64;

    img.onerror = (): void => {
      reject(new Error(''));
    };
    img.onload = (): void => {
      const { width } = img;
      const { height } = img;
      const scale = width / height;
      // let w = 600;
      // let h = parseInt(w / scale);
      let w = 0;
      let h = 0;
      const baseNum = sizeNum;
      if (scale < 1) {
        w = baseNum;
        h = baseNum / scale;
      } else {
        h = baseNum;
        w = scale * baseNum;
      }
      // iosBug
      console.log('orient:', getPhotoOrientation(img));
      const orient = getPhotoOrientation(img);
      if (context) {
        switch (orient) {
          case 6:
            canvas.width = h;
            canvas.height = w;
            context.save();
            context.translate(h / 2, w / 2);
            context.rotate((90 * Math.PI) / 180);
            context.drawImage(img, -w / 2, -h / 2, w, h);
            context.restore();
            break;
          case 3:
            canvas.width = w;
            canvas.height = h;
            context.save();
            context.translate(w / 2, h / 2);
            context.rotate((180 * Math.PI) / 180);
            context.drawImage(img, -w / 2, -h / 2, w, h);
            context.restore();
            break;
          case 8:
            canvas.width = h;
            canvas.height = w;
            context.save();
            context.translate(h / 2, w / 2);
            context.rotate((-90 * Math.PI) / 180);
            context.drawImage(img, -w / 2, -h / 2, w, h);
            context.restore();
            break;
          default:
            canvas.width = w;
            canvas.height = h;
            context.drawImage(img, 0, 0, w, h);
        }
      }
      const imgCompressed = canvas.toDataURL('image/jpeg', 0.92);
      resolve(imgCompressed);
    };
  });
}
const chooseImg = (file: File, sizeNum: number): Promise<string> => {
  return new Promise((resolve, reject) => {
    if (!checkImg(file)) {
      reject(new Error('jpegjpgpng'));
    }
    const reader = new FileReader();
    reader.readAsDataURL(file);
    // base64
    reader.onload = (e): void => {
      // console.log(e && e.target && e.target.result);
      const imageBase64 =
        e && e.target && e.target.result ? e.target.result : '';
      const imageBase64String =
        typeof imageBase64 === 'string' ? imageBase64 : '';
      compressImg(imageBase64String, sizeNum)
        .then((res) => resolve(res))
        .catch((pressError) => reject(pressError));
    };
    reader.onerror = (): void => {
      reject(new Error(''));
    };
  });
};
export default chooseImg;
