// 
const getLevel = (point: number | string, type: string): string => {
  let level = '';
  if (type === 'math' || type === 'maths1' || type === 'maths2') {
    switch (true) {
      case point < 100:
        level = 'D';
        break;
      case point >= 100 && point < 120:
        level = 'C';
        break;
      case point >= 120 && point < 150:
        level = 'B';
        break;
      case point >= 150 && point < 180:
        level = 'A';
        break;
      case point >= 180:
        level = 'S';
        break;
      default:
    }
  }
  if (type === 'physics' || type === 'chemistry') {
    switch (true) {
      case point < 55:
        level = 'D';
        break;
      case point >= 55 && point < 65:
        level = 'C';
        break;
      case point >= 65 && point < 75:
        level = 'B';
        break;
      case point >= 75 && point < 85:
        level = 'A';
        break;
      case point >= 85:
        level = 'S';
        break;
      default:
    }
  }
  return level;
};

export default getLevel;
