import React from 'react';
import {
  TabBarBox,
  TabBarItem,
  TabBarInner,
  ItemIcon,
  ItemTitle,
} from './styles';

interface TabBarProps {
  handelClick: (type: string) => void;
  type: string;
}

const TabBar: React.FC<TabBarProps> = (props) => {
  const { handelClick, type } = props;
  return (
    <>
      <TabBarBox>
        <TabBarInner>
          <TabBarItem
            onClick={(): void => handelClick('library')}
            className={type === 'library' ? 'active' : ''}
          >
            <ItemIcon className="iconfont icon-library" />
            <ItemTitle></ItemTitle>
          </TabBarItem>
          <TabBarItem
            onClick={(): void => handelClick('find')}
            className={type === 'find' ? 'active' : ''}
          >
            <ItemIcon className="iconfont icon-find" />
            <ItemTitle></ItemTitle>
          </TabBarItem>
          <TabBarItem
            onClick={(): void => handelClick('user-center')}
            className={type === 'user-center' ? 'active' : ''}
          >
            <ItemIcon className="iconfont icon-my" />
            <ItemTitle></ItemTitle>
          </TabBarItem>
        </TabBarInner>
      </TabBarBox>
    </>
  );
};

export default TabBar;
