import React, { ReactElement, useEffect } from 'react';
import F2 from '@antv/f2';
import { Card, WhiteSpace, WingBlank } from 'antd-mobile';
import { BottomBlank } from '@/components/TabBar/styles';
import { subjectEnum } from '@/constants/globalDatas';
import pkg from '../../../../package.json';
import { RadarScroll, Table, Td, Th, Tr } from '../styles';
import getLevel from '../utils/getLevel';
import { RadarProps } from '../types';

const themeColor = pkg.theme['brand-primary'];

const Radar: React.FC<RadarProps> = (props) => {
  const { subjectPointsArr } = props;

  useEffect(() => {
    if (subjectPointsArr) {
      const data = subjectPointsArr.map((item) => {
        const { subject, subjectPoint } = item;
        return {
          item: subject,
          score: getLevel(subjectPoint, subject),
        };
      });
      // const data = [
      //   {
      //     item: '',
      //     score: 'S',
      //   },
      //   {
      //     item: '',
      //     score: 'A',
      //   },
      //   {
      //     item: '',
      //     score: 'B',
      //   },
      //   {
      //     item: '',
      //     score: 'C',
      //   },
      //   {
      //     item: '',
      //     score: 'B',
      //   },
      // ];
      const { width } = window.screen;
      const chart = new F2.Chart<typeof data[0]>({
        id: 'radarChart',
        pixelRatio: window.devicePixelRatio,
        padding: 'auto',
        width,
        height: width * 0.8,
      });

      chart.coord('polar');
      chart.source(data, {
        score: {
          type: 'cat',
          values: ['D', 'C', 'B', 'A', 'S'],
        },
      });

      chart.axis('score', {
        label: (text, index, total) => {
          return { top: true, text };
        },
        grid: (text) => {
          return { top: true, text };
        },
        line: {
          top: false,
        },
      });
      chart.axis('item', {
        label: (text, index, total) => {
          return {
            top: true,
            text: subjectEnum[text as keyof typeof subjectEnum],
          };
        },
      });
      chart
        .area()
        .position('item*score')
        .color(`${themeColor}`)
        .animate({
          appear: {
            animation: 'groupWaveIn',
          },
        });
      chart
        .line()
        .position('item*score')
        .color(`${themeColor}`)
        .animate({
          appear: {
            animation: 'groupWaveIn',
          },
        });
      chart
        .point()
        .position('item*score')
        .color(`${themeColor}`)
        .style({
          stroke: '#fff',
          lineWidth: 1,
        })
        .animate({
          appear: {
            delay: 300,
          },
        });
      chart.legend({
        custom: true,
        items: [
          {
            name: '',
            marker: 'circle',
            fill: themeColor,
          },
        ],
      });
      chart.render();
    }
  }, [subjectPointsArr]);

  const getTabelData = (): ReactElement => {
    const tableListData: ReactElement[] = [];
    subjectPointsArr?.forEach((item) => {
      const { subject, subjectPoint } = item;
      tableListData.push(
        <Tr key={subject}>
          <Td>{subjectEnum[subject as keyof typeof subjectEnum]}</Td>
          <Td>{subjectPoint}</Td>
          <Td>{getLevel(subjectPoint, subject)}</Td>
        </Tr>
      );
    });
    return (
      <Table>
        <tbody>
          <Tr>
            <Th></Th>
            <Th></Th>
            <Th></Th>
          </Tr>
          {tableListData}
        </tbody>
      </Table>
    );
  };

  return (
    <RadarScroll>
      <canvas id="radarChart" width="300" height="300" />
      <WingBlank size="lg">
        <WhiteSpace size="lg" />
        <Card>
          <Card.Header title="" />
          <Card.Body>{getTabelData()}</Card.Body>
        </Card>
        <WhiteSpace size="lg" />
      </WingBlank>
      <BottomBlank />
    </RadarScroll>
  );
};

export default Radar;
