import React, { ReactElement, useEffect, useState } from 'react';
import F2 from '@antv/f2';
import { Card, Picker, WhiteSpace, WingBlank } from 'antd-mobile';
import PickItem from '@/components/PickItem';
import { BottomBlank } from '@/components/TabBar/styles';
import APIHelper from '@/helpers/APIHelper';
import getGoalSchool, { PickDataItem, getSubject } from '@/utils/getGoalSchool';
import { RecommendPointAbility, SchoolData } from '@/helpers/types';
import { subjectEnum } from '@/constants/globalDatas';
import pkg from '../../../../package.json';
import {
  PredictBox,
  PredictBoxButton,
  PredictBoxFont,
  PredictScroll,
  PredictSelect,
  Table,
  Td,
  Th,
  Tr,
} from '../styles';
import getLevel from '../utils/getLevel';
import { PredictProps } from '../types';

const themeColor = pkg.theme['brand-primary'];

const Predict: React.FC<PredictProps> = (props) => {
  const [school, setSchool] = useState<string[]>([]);
  const [totalData, setTotalData] = useState<SchoolData>({});
  const [goalSchool, setGoalSchool] = useState<PickDataItem[]>([]); // 
  const [ejuSubject, setEjuSubject] = useState<PickDataItem[]>([]); // 
  const [department, setDepartment] = useState<string[]>([]); // 
  const [probability, setProbability] = useState(0); // 
  const [recommendPoint, setRecommendPoint] = useState<RecommendPointAbility>({
    english: '',
    jap: 0,
    jap_writing: 0,
    math: 0,
    physics: 0,
    chemistry: 0,
  }); // 
  const { subjectPointsArr } = props;

  useEffect(() => {
    if (subjectPointsArr || recommendPoint) {
      const recommendPointData = { ...recommendPoint };
      const data: { [key: string]: string }[] = [];
      subjectPointsArr?.forEach((item) => {
        const { subject, subjectPoint } = item;
        data.push({
          item: subject,
          type: 'my',
          score: getLevel(subjectPoint, subject),
        });
      });
      Object.keys(recommendPointData).forEach((item) => {
        console.log('recommendPointData+++++++item', item);
        data.push({
          item,
          type: 'you',
          score:
            item === 'english'
              ? recommendPointData[item]
              : getLevel(
                  recommendPointData[item as keyof typeof recommendPointData],
                  item
                ),
        });
      });
      // const data = [
      //   {
      //     item: '',
      //     type: 'my',
      //     score: 'S',
      //   },
      //   {
      //     item: '',
      //     type: 'my',
      //     score: 'A+',
      //   },
      //   {
      //     item: '',
      //     type: 'my',
      //     score: 'B+',
      //   },
      //   {
      //     item: '',
      //     type: 'my',
      //     score: 'C+',
      //   },
      //   {
      //     item: '',
      //     type: 'my',
      //     score: 'A-',
      //   },
      //   {
      //     item: '',
      //     type: 'you',
      //     score: 'B+',
      //   },
      //   {
      //     item: '',
      //     type: 'you',
      //     score: 'C-',
      //   },
      //   {
      //     item: '',
      //     type: 'you',
      //     score: 'A+',
      //   },
      //   {
      //     item: '',
      //     type: 'you',
      //     score: 'A+',
      //   },
      //   {
      //     item: '',
      //     type: 'you',
      //     score: 'B-',
      //   },
      // ];
      const { width } = window.screen;
      const chart = new F2.Chart<typeof data[0]>({
        id: 'predictChart',
        pixelRatio: window.devicePixelRatio,
        padding: 30,
        width,
        height: width * 0.8,
        appendPadding: [15, 15, 15, 20],
      });

      chart.coord('polar');
      chart.source(data, {
        score: {
          type: 'cat',
          values: ['D', 'C', 'B', 'A', 'S'],
        },
      });

      chart.axis('score', {
        label: (text, index, total) => {
          console.log('label++++++score', text, index, total);
          return { top: true, text };
        },
        grid: (text) => {
          console.log(text);
          return { top: true, text };
        },
        line: {
          top: false,
        },
      });
      chart.axis('item', {
        label: (text, index, total) => {
          console.log('label++++++item', text, index, total);
          return {
            top: true,
            text: subjectEnum[text as keyof typeof subjectEnum],
          };
        },
      });
      chart
        .area()
        .position('item*score')
        .color('type', (type) => {
          console.log(type);
          if (type === 'my') {
            return themeColor;
          }
          return '#BFBFBF';
        })
        .animate({
          appear: {
            animation: 'groupWaveIn',
          },
        });
      chart
        .line()
        .position('item*score')
        .color('type', (type) => {
          console.log(type);
          if (type === 'my') {
            return themeColor;
          }
          return '#BFBFBF';
        })
        .animate({
          appear: {
            animation: 'groupWaveIn',
          },
        });
      chart
        .point()
        .position('item*score')
        .color('type', (type) => {
          console.log(type);
          if (type === 'my') {
            return themeColor;
          }
          return '#BFBFBF';
        })
        .style({
          stroke: '#fff',
          lineWidth: 1,
        })
        .animate({
          appear: {
            delay: 300,
          },
        });
      chart.legend({
        custom: true,
        verticalAlign: 'top',
        position: 'left',
        items: [
          {
            name: '',
            marker: 'circle',
            fill: themeColor,
          },
          {
            name: '',
            marker: 'circle',
            fill: '#BFBFBF',
          },
        ],
      });
      chart.render();
    }
  }, [recommendPoint, subjectPointsArr]);
  const getTabelData = (): ReactElement => {
    const recommendPointData = { ...recommendPoint };
    const tableListData: ReactElement[] = [];
    const sourceData = {} as {
      [key: string]: {
        point?: number;
        level?: string;
        goalPoint?: number;
        goalLevel?: string;
      };
    };
    subjectPointsArr?.forEach((item) => {
      const { subject, subjectPoint } = item;
      if (!sourceData[subject]) {
        sourceData[subject] = {};
      }
      sourceData[subject].point = subjectPoint;
      sourceData[subject].level = getLevel(subjectPoint, subject);
    });
    Object.keys(recommendPointData).forEach((item) => {
      if (!sourceData[item]) {
        sourceData[item] = {};
      }
      let point = 0;
      if (item === 'english') {
        // point = recommendPointData[item as keyof typeof recommendPointData].toefl;
        // 
        point = 0;
      } else {
        point = Number(
          recommendPointData[item as keyof typeof recommendPointData]
        );
      }
      console.log('sourceData+++', sourceData);

      sourceData[item].goalPoint = point;
      sourceData[item].goalLevel = getLevel(point, item);
    });
    Object.keys(sourceData).forEach((item) => {
      const itemData = sourceData[item];
      const { point, level, goalPoint, goalLevel } = itemData;
      tableListData.push(
        <Tr key={item}>
          <Td>{subjectEnum[item as keyof typeof subjectEnum]}</Td>
          <Td>{point}</Td>
          <Td>{goalPoint}</Td>
          <Td>{level}</Td>
          <Td>{goalLevel}</Td>
        </Tr>
      );
    });
    return (
      <Table>
        <tbody>
          <Tr>
            <Th></Th>
            <Th></Th>
            <Th></Th>
            <Th></Th>
            <Th></Th>
          </Tr>
          {tableListData}
        </tbody>
      </Table>
    );
  };
  // 
  const handleSchool = (value: string[]): void => {
    console.log(value, 'value+++');

    const subjectArr = getSubject(
      totalData[Number(value[0]) as keyof typeof totalData].departments
    );
    setEjuSubject(subjectArr);
    setSchool(value);
    setDepartment([]);
  };

  // 
  const handlePredict = (): void => {
    const param = {
      school_id: Number(school[0]),
      math: 200,
      ja: 400,
      phy: 90,
      che: 100,
      jaw: 50,
      department: department[0],
    };
    APIHelper.predict(param).then((res) => {
      console.log(res.code);
      if (res.code === 204) {
        setProbability(res.data.probability);
        setRecommendPoint(res.data.recommendPoint.ability);
      }
    });
  };
  useEffect(() => {
    APIHelper.getSchool().then((res) => {
      setTotalData(res.data);
      const filterArr = getGoalSchool(res.data);
      setGoalSchool(filterArr);
    });
  }, []);
  return (
    <PredictScroll>
      <PredictSelect>
        <WhiteSpace size="lg" />
        <Picker
          data={goalSchool}
          cols={1}
          onOk={(value): void => handleSchool(value)}
        >
          <PickItem
            placeholder=""
            value={
              totalData[Number(school[0]) as keyof typeof totalData]?.schoolName
            }
          />
        </Picker>

        <Picker
          data={ejuSubject}
          cols={1}
          onOk={(value): void => setDepartment(value)}
        >
          <PickItem placeholder="" value={department[0]} />
        </Picker>
      </PredictSelect>

      <PredictBox>
        <PredictBoxFont>
          :{(probability * 100).toFixed(2)}%
        </PredictBoxFont>
        <PredictBoxButton onClick={(): void => handlePredict()}>
          
        </PredictBoxButton>
      </PredictBox>
      <canvas id="predictChart" width="300" height="300" />
      <WingBlank size="lg">
        <WhiteSpace size="lg" />
        <Card>
          <Card.Header title="" />
          <Card.Body>{getTabelData()}</Card.Body>
        </Card>
        <WhiteSpace size="lg" />
      </WingBlank>
      <BottomBlank />
    </PredictScroll>
  );
};

export default Predict;
