import React, {
  ReactElement,
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useState,
} from 'react';
import {
  Accordion,
  Picker,
  WhiteSpace,
  Button,
  Card,
  Toast,
} from 'antd-mobile';
import PickItem from '@/components/PickItem';
import { useHistory } from 'react-router-dom';
import { ProviderContext } from '@/reducer/context';
import { useLazyQuery, useQuery } from '@apollo/client';
import { QUERY_SUBJECT, SUBJECT_TAGS, USER_SHOW } from '@/helpers/APIHelper';
import { examEnum, subjectEnum } from '@/constants/globalDatas';
import { BottomBlank } from '@/components/TabBar/styles';
import {
  AccordionBottom,
  AccordionBox,
  AccordionItem,
  AccordionLeft,
  AccordionLeftStarBox,
  AccordionLeftTite,
  AccordionRight,
  Content,
  Star,
  StarBox,
  StarFont,
  TabBarBox,
  TabBarItem,
} from './styles';
import stringArrToPickArr from './utils/stringArrToPickArr';
import { filterSourceType, filterExamId } from './utils/filterArr';
import dateToYear from './utils/dateToYear';

const Library: React.FC = () => {
  // const [ejuSubjectValue, setEjuSubjectValue] = useState<string[]>(['maths1']); // ,maths1
  const [subjectArr, setSubjectArr] = useState([{ value: '', label: '' }]); // 
  const { data: userInfo } = useQuery(USER_SHOW);
  const [subjectReq, { data: sujectData, loading: subjectLoading }] =
    useLazyQuery(QUERY_SUBJECT); // 
  const [
    subjectTagsReq,
    { data: subjectTagsData, loading: subjectTagLoading },
  ] = useLazyQuery(SUBJECT_TAGS); // 

  const {
    subjectTags,
    getUserTagAbilities,
    subjectExams = [],
    getExamHistoryBySubject = [],
  } = subjectTagsData || {};
  const filterExam = useMemo(
    () => filterSourceType(subjectExams),
    [subjectExams]
  ); // 
  const filterExamHis = useMemo(
    () => filterExamId(getExamHistoryBySubject),
    [getExamHistoryBySubject]
  ); // 
  const { state, dispatch } = useContext(ProviderContext);
  const { tabType, ejuSubjectValue } = state;
  const history = useHistory();
  // 
  const handleTabChange = (type: string): void => {
    dispatch?.({ type: 'setTabType', tabType: type });
  };
  // tabTypesourceType
  const tabTypeToSourceType = useMemo(() => {
    switch (tabType) {
      case 'quick':
        return 'gaokao';
      case 'topic':
      case 'exam':
        return 'center';
      default:
        return '';
    }
  }, [tabType]);
  // 
  const setAbilities = useCallback(
    (prevTag: string): ReactElement => {
      let abilities = 0;
      const startArr = [];
      getUserTagAbilities?.forEach(
        (item: { tagId: string; abilityValue: string }) => {
          const { tagId, abilityValue } = item;
          if (prevTag === tagId) {
            abilities = Math.round(Number(abilityValue) / 20);
          }
        }
      );
      for (let i = 1; i <= 5; i += 1) {
        if (abilities === 5) {
          startArr.push(<Star key={i} className="all iconfont icon-star1" />);
        } else if (i <= abilities) {
          startArr.push(
            <Star key={i} className="active iconfont icon-star1" />
          );
        } else {
          startArr.push(<Star key={i} />);
        }
      }
      return <>{startArr}</>;
    },
    [getUserTagAbilities]
  );

  // 
  const getQuick = useCallback(() => {
    return subjectTags?.tags?.map((item: any) => {
      const { _id, name, children = [] } = item;
      return (
        <div key={_id}>
          <Card>
            <Card.Header title={name} />
            <Card.Body>
              <Accordion>
                {children.map((item2: any) => {
                  const {
                    name: name2,
                    _id: _id2,
                    children: children2 = [],
                  } = item2;
                  return (
                    <Accordion.Panel
                      header={name2}
                      key={_id2}
                      style={{ marginBottom: '10px' }}
                    >
                      <AccordionBox>
                        {children2.map((item3: any) => {
                          const { name: name3, _id: _id3 } = item3;
                          const abilities = setAbilities(_id3);
                          return (
                            <AccordionItem key={_id3}>
                              <AccordionLeftTite>{name3}</AccordionLeftTite>
                              <AccordionBottom>
                                <AccordionLeft>
                                  <AccordionLeftStarBox>
                                    <StarFont></StarFont>
                                    <StarBox>{abilities}</StarBox>
                                  </AccordionLeftStarBox>
                                </AccordionLeft>
                                <AccordionRight>
                                  <Button
                                    type="primary"
                                    size="small"
                                    inline
                                    onClick={(): void =>
                                      history.push(
                                        `/quick?subject=${
                                          subjectTags?.subject || ''
                                        }&tagId=${
                                          _id3 || ''
                                        }&tag=${name3}&sourceType=${tabTypeToSourceType}`
                                      )
                                    }
                                  >
                                    
                                  </Button>
                                </AccordionRight>
                              </AccordionBottom>
                            </AccordionItem>
                          );
                        })}
                      </AccordionBox>
                    </Accordion.Panel>
                  );
                })}
              </Accordion>
            </Card.Body>
          </Card>
          <WhiteSpace size="lg" />
        </div>
      );
    });
  }, [
    history,
    setAbilities,
    subjectTags?.subject,
    subjectTags?.tags,
    tabTypeToSourceType,
  ]);

  // 
  const handleToExamInfo = useCallback(
    (data) => {
      dispatch?.({ type: 'setExam', exam: data });
      history.push('/exam-info');
    },
    [dispatch, history]
  );
  // 
  const getExam = useCallback(() => {
    return (
      <Accordion>
        {filterExam.map((item) => {
          return (
            <Accordion.Panel
              header={examEnum[item.sourceType as keyof typeof examEnum]}
              style={{ marginBottom: '20px' }}
              key={item.sourceType}
            >
              <AccordionBox>
                {item.children.map((val) => {
                  const { date: examDate } = val;
                  return (
                    <AccordionItem key={val._id}>
                      <AccordionLeftTite>
                        {dateToYear(examDate)}
                      </AccordionLeftTite>
                      <AccordionBottom>
                        <AccordionLeft>
                          <AccordionLeftStarBox>
                            <StarFont>
                              
                              {filterExamHis[val._id]?.bestPoints}
                            </StarFont>
                          </AccordionLeftStarBox>
                        </AccordionLeft>
                        <AccordionRight>
                          <Button
                            type="primary"
                            size="small"
                            inline
                            onClick={(): void =>
                              handleToExamInfo({
                                exam: val,
                                examHis: filterExamHis[val._id],
                              })
                            }
                          >
                            
                          </Button>
                        </AccordionRight>
                      </AccordionBottom>
                    </AccordionItem>
                  );
                })}
              </AccordionBox>
            </Accordion.Panel>
          );
        })}
      </Accordion>
    );
  }, [filterExam, filterExamHis, handleToExamInfo]);

  // 
  const getTopic = useCallback(() => {
    return subjectTags?.tags?.map((item: any) => {
      const { _id, name } = item;
      const abilities = setAbilities(_id);

      return (
        <AccordionItem key={_id}>
          <AccordionLeftTite>{name}</AccordionLeftTite>
          <AccordionBottom>
            <AccordionLeft>
              <AccordionLeftStarBox>
                <StarFont></StarFont>
                <StarBox>{abilities}</StarBox>
              </AccordionLeftStarBox>
            </AccordionLeft>
            <AccordionRight>
              <Button
                type="primary"
                size="small"
                inline
                onClick={(): void =>
                  history.push(
                    `/topic?subject=${subjectTags?.subject || ''}&tagId=${
                      _id || ''
                    }&tag=${name}`
                  )
                }
              >
                
              </Button>
            </AccordionRight>
          </AccordionBottom>
        </AccordionItem>
      );
    });
  }, [history, setAbilities, subjectTags?.subject, subjectTags?.tags]);

  // 
  const changeEjuSubject = (value: string[]): void => {
    dispatch?.({ type: 'setEjuSubjectValue', ejuSubjectValue: value });
  };

  // 
  useEffect(() => {
    subjectReq({
      variables: {
        sourceType: tabTypeToSourceType,
      },
    });
  }, [subjectReq, tabTypeToSourceType]);
  // 
  useEffect(() => {
    const subject = ejuSubjectValue?.[0];
    subject &&
      subjectTagsReq({
        variables: {
          sourceType: tabTypeToSourceType,
          subject,
        },
      });
  }, [ejuSubjectValue, subjectTagsReq, tabTypeToSourceType]);
  // 
  useEffect(() => {
    const { typeSubjects } = sujectData || {};
    if (typeSubjects) {
      setSubjectArr(stringArrToPickArr(typeSubjects, subjectEnum));
    }
  }, [sujectData]);
  // loading
  useEffect(() => {
    (subjectLoading || subjectTagLoading) && Toast.loading('', 0);
    !subjectLoading && !subjectTagLoading && Toast.hide();
  }, [subjectLoading, subjectTagLoading]);

  // 
  useEffect(() => {
    const userData = userInfo?.getUserInfo;
    if (userData) {
      dispatch?.({ type: 'setUserInfo', userInfo: userData });
      // 
      if (userData.avatar) {
        const prevImg = new Image();
        prevImg.src = userData.avatar;
      }
    }
  }, [dispatch, userInfo?.getUserInfo]);
  return (
    <Content>
      <TabBarBox>
        <TabBarItem
          className={tabType === 'quick' ? 'active' : ''}
          onClick={(): void => handleTabChange('quick')}
        >
          
        </TabBarItem>
        <TabBarItem
          className={tabType === 'topic' ? 'active' : ''}
          onClick={(): void => handleTabChange('topic')}
        >
          
        </TabBarItem>
        <TabBarItem
          className={tabType === 'exam' ? 'active' : ''}
          onClick={(): void => handleTabChange('exam')}
        >
          
        </TabBarItem>
      </TabBarBox>
      <WhiteSpace size="xl" />

      <Picker
        data={subjectArr}
        cols={1}
        value={ejuSubjectValue}
        onOk={(value: string[]): void => changeEjuSubject(value)}
      >
        <PickItem placeholder="" />
      </Picker>
      {tabType === 'quick' && getQuick()}
      {tabType === 'topic' && getTopic()}
      {tabType === 'exam' && getExam()}
      <BottomBlank />
    </Content>
  );
};

export default Library;
