import React, { useContext, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useMutation, useQuery } from '@apollo/client';
import {
  QUERY_MSG_NUM,
  UPDATE_USER_INFO,
  USER_SHOW,
} from '@/helpers/APIHelper';
import { ProviderContext } from '@/reducer/context';
import {
  PersonBg,
  AvatarBox,
  PersonInner,
  Avatar,
  PersonBgInner,
  NickBox,
  NickName,
  Sign,
  FucList,
  FucListItem,
  FucListItemTop,
  FucListItemBottom,
  MenuBox,
  MenuItem,
  MenuLabel,
  MenuIcon,
  MsgNum,
  AvatarUpload,
} from './styles';
import chooseImg from './utils/chooseImg';
import avatarDefault from './images/avatar-default.jpg';

const UserCenter: React.FC = () => {
  const { data = {} } = useQuery(USER_SHOW, {
    fetchPolicy: 'cache-and-network',
  });
  const { data: msgNum } = useQuery(QUERY_MSG_NUM, {
    fetchPolicy: 'cache-and-network',
  });
  const { state } = useContext(ProviderContext);
  const { userInfo } = state;
  const { nickName, signature, avatar } = data?.getUserInfo || userInfo || {};
  const [avatarBase64, setAvatarBase64] = useState(avatar || '');
  const [updateUserInfo] = useMutation(UPDATE_USER_INFO, {
    onCompleted: () => console.log(''),
    onError: () => console.log(''),
  });

  const history = useHistory();
  const avatarStr = avatarBase64 || avatarDefault;

  console.log('avatarStr+++++', avatarStr);
  // 
  const handleMenu = (type: string): void => {
    switch (type) {
      case 'msg':
        history.push('/msg');
        break;
      case 'personal':
        history.push('/complete-all?completeType=update');
        break;
      case 'setting':
        history.push('/setting');
        break;
      default:
    }
  };
  // 
  // const getMessageNum = (): number => {
  //   let num = 0;
  //   userMessages?.forEach((item: { isRead: boolean }) => {
  //     if (!item.isRead) {
  //       num += 1;
  //     }
  //   });
  //   return num;
  // };

  // 
  const getImg = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const fileData = e.target.files;
    if (fileData && fileData[0]) {
      // 
      const sizeNum =
        (document.getElementById('avatar_box')?.offsetWidth || 0) *
        window.devicePixelRatio;
      chooseImg(fileData[0], sizeNum).then((res) => {
        updateUserInfo({
          variables: {
            userInfoInput: {
              avatar: res,
            },
          },
        });
        setAvatarBase64(res);
        console.log(res);
      });
    }
  };

  // 
  useEffect(() => {
    if (avatar) {
      const img = new Image();
      img.src = avatar;
      img.onload = (): void => {
        setAvatarBase64(avatar);
      };
    }
  }, [avatar]);
  return (
    <>
      <PersonBg>
        <PersonBgInner bg={avatarStr} />
        <PersonInner>
          <AvatarBox id="avatar_box">
            <Avatar src={avatarStr} />
            <AvatarUpload
              type="file"
              accept="image/*"
              onChange={(e): void => getImg(e)}
            />
          </AvatarBox>
          <NickBox>
            <NickName>{nickName}</NickName>
            <Sign>{signature}</Sign>
          </NickBox>
        </PersonInner>
      </PersonBg>
      <FucList>
        <FucListItem onClick={(): void => history.push('/record')}>
          <FucListItemTop />
          <FucListItemBottom></FucListItemBottom>
        </FucListItem>
      </FucList>
      <MenuBox>
        <MenuItem onClick={(): void => handleMenu('personal')}>
          <MenuLabel>
            <MenuIcon className="iconfont icon-personal" />
            
          </MenuLabel>
        </MenuItem>
        <MenuItem onClick={(): void => handleMenu('msg')}>
          <MenuLabel>
            <MenuIcon className="iconfont icon-msg" />
            
          </MenuLabel>
          <MsgNum>{msgNum?.getUnreadMessageCount || 0}</MsgNum>
        </MenuItem>
        <MenuItem onClick={(): void => handleMenu('setting')}>
          <MenuLabel>
            <MenuIcon className="iconfont icon-setting" />
            
          </MenuLabel>
        </MenuItem>
      </MenuBox>
    </>
  );
};

export default UserCenter;
