import React, {
  ReactNode,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import {
  FIND_GENERAL_ABILITY,
  FIND_SUBJECT,
  PREDICTED_POINTS,
  QUERY_SUBJECT,
  USER_SHOW,
} from '@/helpers/APIHelper';
import { useQuery } from '@apollo/client';
import { level, subjectEnum } from '@/constants/globalDatas';
import Ladder from './Ladder';
import Strength from './Strength';
import Predict from './Predict';
import {
  IconStar,
  InfoBox,
  InfoItem,
  InfoLi,
  Page,
  TabBox,
  TabItem,
} from './styles';
import stringArrToPickArr, {
  PickValueArr,
} from '../Library/utils/stringArrToPickArr';

const Find: React.FC = () => {
  const { data = {} } = useQuery(USER_SHOW);
  const { data: generalAbility = 0 } = useQuery(FIND_GENERAL_ABILITY);
  // 
  const { data: sujectData } = useQuery(QUERY_SUBJECT, {
    variables: { sourceType: 'center' },
  });
  // 
  const { data: findSujectData } = useQuery(FIND_SUBJECT);
  // 
  const { data: predicterPoints } = useQuery(PREDICTED_POINTS);
  console.log(predicterPoints, 'predicterPoints+++++');
  const { totalRankThisWeek, findPointsSubject, totalPoint } =
    predicterPoints?.findPredictedPoints || {};
  // 
  const subjectArr = useMemo(
    () => sujectData?.typeSubjects || [],
    [sujectData?.typeSubjects]
  );
  // 
  const findSubjectArr = useMemo(
    () => findSujectData?.findSubjects || [],
    [findSujectData?.findSubjects]
  );
  // 
  const abilityValue = Math.round(generalAbility?.findGeneralAbility / 20);
  const [tabType, setTabType] = useState('strength'); // tab
  const [rankNum, setRankNum] = useState(0); // 
  const [subjectFilterArr, setSubjectFilterArr] = useState<PickValueArr[]>([]); // 
  const [findSubjectFilterArr, setFindSubjectFilterArr] = useState<
    PickValueArr[]
  >([]); // 
  const [strengthTab, setStrengthTab] = useState(0);
  // tab
  const handleTabChange = (type: string): void => {
    setTabType(type);
  };
  // 
  const getTabContent = useCallback((): ReactNode => {
    let ele = null;
    switch (tabType) {
      case 'strength':
        ele = (
          <Strength
            subjectArr={subjectFilterArr}
            subjectPointsArr={findPointsSubject}
            handleStrengthTabChang={handleStrengthTabChang}
            strengthTab={strengthTab}
          />
        );
        break;
      case 'ladder':
        ele = (
          <Ladder
            subjectArr={findSubjectFilterArr}
            rankNum={rankNum}
            subjectPointsArr={findPointsSubject}
          />
        );
        break;
      case 'predict':
        ele = <Predict subjectPointsArr={findPointsSubject} />;
        break;
      default:
    }
    return ele;
  }, [
    findPointsSubject,
    findSubjectFilterArr,
    rankNum,
    strengthTab,
    subjectFilterArr,
    tabType,
  ]);

  const { nickName } = data?.getUserInfo || {};

  // 
  const getStar = useCallback(() => {
    const starArr = [];
    for (let i = 1; i <= 5; i += 1) {
      starArr.push(
        <IconStar
          className={i <= abilityValue ? 'iconfont icon-star1' : ''}
          key={i}
        />
      );
    }
    return starArr;
  }, [abilityValue]);

  // 
  const getInfo = useCallback((): JSX.Element => {
    const Item1 = <InfoItem>{nickName}</InfoItem>;
    let Item2 = null;
    let Item3 = null;
    let Item4 = null;
    switch (tabType) {
      case 'strength':
        if (strengthTab === 0) {
          Item2 = <InfoItem>()</InfoItem>;
          Item4 = <InfoItem>{totalPoint}</InfoItem>;
        }
        if (strengthTab === 1) {
          Item2 = <InfoItem></InfoItem>;
          Item4 = <InfoItem>{getStar()}</InfoItem>;
        }
        Item3 = (
          <InfoItem>
            
            {level[abilityValue as keyof typeof level]}
          </InfoItem>
        );
        break;
      case 'ladder':
        Item2 = <InfoItem></InfoItem>;
        Item3 = <InfoItem>{totalPoint}</InfoItem>;
        Item4 = <InfoItem>{rankNum}</InfoItem>;
        break;
      case 'predict':
        Item2 = <InfoItem>()</InfoItem>;
        Item3 = (
          <InfoItem>
            
            {level[abilityValue as keyof typeof level]}
          </InfoItem>
        );
        Item4 = <InfoItem>{totalPoint}</InfoItem>;
        break;
      default:
    }
    return (
      <InfoBox>
        <InfoLi>
          {Item1}
          {Item2}
        </InfoLi>
        <InfoLi>
          {Item3}
          {Item4}
        </InfoLi>
      </InfoBox>
    );
  }, [
    abilityValue,
    getStar,
    nickName,
    rankNum,
    strengthTab,
    tabType,
    totalPoint,
  ]);
  // tab
  const handleStrengthTabChang = (index: number): void => {
    console.log(index);
    setStrengthTab(index);
  };
  // 
  useEffect(() => {
    if (subjectArr?.length > 0) {
      setSubjectFilterArr(stringArrToPickArr(subjectArr, subjectEnum));
    }
  }, [subjectArr]);
  // 
  useEffect(() => {
    if (findSubjectArr?.length > 0) {
      setFindSubjectFilterArr(stringArrToPickArr(findSubjectArr, subjectEnum));
    }
  }, [findSubjectArr]);
  // 
  useEffect(() => {
    setRankNum(totalRankThisWeek);
  }, [totalRankThisWeek]);
  return (
    <Page>
      <TabBox>
        <TabItem
          className={tabType === 'strength' ? 'active' : ''}
          onClick={(): void => handleTabChange('strength')}
        >
          
        </TabItem>
        <TabItem
          className={tabType === 'ladder' ? 'active' : ''}
          onClick={(): void => handleTabChange('ladder')}
        >
          
        </TabItem>
        <TabItem
          className={tabType === 'predict' ? 'active' : ''}
          onClick={(): void => handleTabChange('predict')}
        >
          
        </TabItem>
      </TabBox>
      <>{getInfo()}</>
      <>{getTabContent()}</>
    </Page>
  );
};

export default Find;
