import React from 'react';
import { Tabs } from 'antd-mobile';
import Radar from './Radar';
import SubjectParsing from './SubjectParsing';
import { StrengthProps } from '../types';

const Strength: React.FC<StrengthProps> = (props) => {
  const { subjectArr, subjectPointsArr, handleStrengthTabChang, strengthTab } =
    props;
  const handleTabChange = (index: number): void => {
    handleStrengthTabChang(index);
  };
  return (
    <Tabs
      tabs={[
        { title: '', sub: '1' },
        { title: '/', sub: '2' },
      ]}
      page={strengthTab}
      onChange={(tab, index): void => handleTabChange(index)}
    >
      <Radar subjectPointsArr={subjectPointsArr} />
      <SubjectParsing subjectArr={subjectArr} />
    </Tabs>
  );
};

export default Strength;
