import React, { useEffect, useRef, useState } from 'react';
import F2 from '@antv/f2';
import PickItem from '@/components/PickItem';
import { Picker, Toast } from 'antd-mobile';
import { FIND_TAG, SUBJECT_TAGS_ONLY } from '@/helpers/APIHelper';
import { useLazyQuery } from '@apollo/client';
import { PickValueArr } from '@/pages/Library/utils/stringArrToPickArr';
import { subjectEnum } from '@/constants/globalDatas';
import getLabelFromValue from '@/utils/getLabelFromValue';
import { SelectBox, SelectBoxItem } from '../styles';

// interface SubjectParsingItem {
//   chemistry: { [key: string]: number };
//   math: { [key: string]: number };
//   physicals: { [key: string]: number };
// }
// interface SubjectParsingProps {
//   userInfo?: SubjectParsingItem;
//   generalInfo?: {
//     average?: SubjectParsingItem;
//     top: SubjectParsingItem;
//   };
// }

interface SelectData {
  value: string;
  label: string;
}
interface SubjectParsingProps {
  subjectArr: PickValueArr[];
}

interface ChartData {
  date: number;
  type: string;
  value: number;
}

interface TagsData {
  _id: string;
  name: string;
  children: {
    _id: string;
    name: string;
  }[];
}

interface SourceItem {
  type: string;
  date: number;
  value: number;
}
const SubjectParsing: React.FC<SubjectParsingProps> = (props) => {
  // 
  const [pointsData, setPointData] = useState<SelectData[]>([]);
  const [pointsValue, setPointValue] = useState<string[]>(['']);
  // 
  const [subjectvalue, setSubjectValue] = useState<string[]>(['maths1']);
  const [getTags, { data: tags, loading: tagsLoading }] =
    useLazyQuery(SUBJECT_TAGS_ONLY);
  const [getAbilityTag] = useLazyQuery(FIND_TAG, {
    fetchPolicy: 'cache-and-network',
    onCompleted: (res) => handleAbility(res),
  });
  const tagsArrRef = useRef<TagsData[]>([]);
  const chartRef = useRef<any>();

  const { subjectArr } = props;
  // 
  const handleSubjectChange = (value: string[]): void => {
    setSubjectValue(value);
  };
  // 
  const handlePointsChange = (value: string[]): void => {
    setPointValue(value);
  };
  // 
  const handleAbility = (data: {
    findAbilityTag: {
      abilityValues: number[];
      tagName: string;
    }[];
  }): void => {
    const { findAbilityTag } = data || {};
    console.log(findAbilityTag);
    const source: SourceItem[] = [];

    findAbilityTag?.forEach((item) => {
      const { abilityValues, tagName } = item;
      for (let i = 1; i <= 6; i += 1) {
        source.push({
          type: tagName,
          date: i,
          value: abilityValues?.[i] ? Math.round(abilityValues[i] / 20) : 0,
        });
      }
    });
    showParsing(source);
  };
  // 
  useEffect(() => {
    getTags({
      variables: {
        subject: subjectvalue[0],
        sourceType: 'center',
      },
    });
  }, [getTags, subjectvalue]);

  // 
  useEffect(() => {
    const tagsData = tags?.subjectTags?.tags || [];
    const tagsArr =
      tagsData.map((item: { name: string; _id: number }) => {
        const { name, _id } = item;
        return {
          label: name,
          value: _id,
        };
      }) || [];
    setPointValue([tagsArr?.[0]?.value || '']);
    setPointData(tagsArr);
    tagsArrRef.current = tagsData;
  }, [tags?.subjectTags?.tags]);

  // loading
  useEffect(() => {
    tagsLoading ? Toast.loading('', 0) : Toast.hide();
  }, [tagsLoading]);

  // 
  useEffect(() => {
    const tagId = pointsValue?.[0];
    if (tagId) {
      getAbilityTag({
        variables: {
          tagId,
        },
      });
    }
  }, [getAbilityTag, pointsValue]);

  // 
  // useEffect(() => {
  //   console.log(pointsValue);
  //   const tagsId = pointsValue[0];
  //   // tag
  //   if (tagsId) {
  //     tagsArrRef?.current?.forEach((item) => {
  //       if (item._id === tagsId) {
  //         console.log(item.children);
  //         item.children.forEach((item2) => {
  //           getAbilityTag({
  //             variables: {
  //               tagId: item2._id,
  //             },
  //           });
  //         });
  //       }
  //     });
  //   }
  // }, [getAbilityTag, pointsValue]);
  // 
  const showParsing = (data: ChartData[]): void => {
    const { width } = window.screen;
    const chart = new F2.Chart<typeof data[0]>({
      id: 'columnarChart',
      pixelRatio: window.devicePixelRatio,
      padding: 'auto',
      width,
      height: width * 0.8,
      appendPadding: [15, 15, 15, 20],
    });
    chartRef.current = chart;
    // chart.source(data, {
    //   percent: {
    //     type: 'linear',
    //     alias: '(%)',
    //     min: 0,
    //     max: 100,
    //     tickCount: 6,
    //   },
    // });

    // chart.interval().position('genre*percent').color('genre');
    // chart.legend({
    //   align: 'right',
    //   itemWidth: null,
    // });
    // chart.guide().text({
    //   position: ['min', 'max'],
    //   content: '(%)',
    //   style: {
    //     textBaseline: 'middle',
    //   },
    //   offsetY: -35,
    // });
    // chart.render();
    // const offset = -5;
    // const canvas = chart.get('canvas');
    // const group = canvas.addGroup();
    // data.forEach((obj) => {
    //   const point = chart.getPosition(obj);
    //   group.addShape('text', {
    //     attrs: {
    //       x: point.x,
    //       y: point.y + offset,
    //       text: `${obj.percent}%`,
    //       textAlign: 'center',
    //       textBaseline: 'bottom',
    //       fill: '#808080',
    //     },
    //   });
    // });
    chart.source(data);
    chart.scale('date', {
      ticks: ['1', '2', '3', '4', '5', '6'],
    });
    chart.scale('value', {
      ticks: ['0', '1', '2', '3', '4', '5'],
    });
    chart.axis('date', {
      label: function label(text, index, total) {
        return {
          text: `${text}`,
        };
      },
    });
    chart.axis('value', {
      label: function label(text, index, total) {
        return {
          text: `${text}`,
        };
      },
    });
    chart.line().position('date*value').color('type');
    chart.point().position('date*value').color('type');
    chart.render();
  };

  return (
    <>
      <SelectBox>
        <SelectBoxItem>
          <Picker
            data={subjectArr}
            cols={1}
            onOk={handleSubjectChange}
            value={subjectvalue}
          >
            <PickItem
              placeholder=""
              value={subjectEnum[subjectvalue[0] as keyof typeof subjectEnum]}
            />
          </Picker>
        </SelectBoxItem>
        <SelectBoxItem>
          <Picker
            data={pointsData}
            cols={1}
            onOk={handlePointsChange}
            value={pointsValue}
          >
            <PickItem
              placeholder=""
              value={getLabelFromValue(pointsData, pointsValue[0] || '')}
            />
          </Picker>
        </SelectBoxItem>
      </SelectBox>
      <canvas id="columnarChart" />
    </>
  );
};

export default SubjectParsing;
